* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f2f2f2;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.form {
  max-width: 400px;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

label {
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 10px;
  font-weight: bold;
  display: flex;
}

input[type="number"] {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 10px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 10px 20px;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

p a {
  color: #007bff;
  text-decoration: none;
}

p a:hover {
  text-decoration: underline;
}

